import React, { useState } from 'react';
import './App.css';
import Timeline from './components/Timeline'
import Info from './components/Info'
import timelineData from './data'

function App() {
  const [d, setD] = useState(timelineData)
  const [num, setNum] = useState(0)
  const [animate, setAnimate] = useState(false)

  function handler (index){
        if(index < 0){
          index = d.length-1
        }

        if(index >= d.length){
          index=0
        }

        if(animate){
          return;
        }

        let tempD = [...d];
        for(let i =0; i<tempD.length; i++){
                tempD[i].active = false
                tempD[i].highlight = false
        }

        tempD[index].active = true;

        for(let i = 0; i<index; i++){
            tempD[i].highlight = true
        }

        setD(tempD)
        setNum(index)
        changeTheme(index)
        setAnimate(true)
  }

  function changeTheme(index){
    if(d[index].type === 0){
      document.documentElement.style.setProperty('--primary','tomato')
    }
    if(d[index].type === 1){
      document.documentElement.style.setProperty('--primary','#ab20fd')
    }
    if(d[index].type === 2){
      document.documentElement.style.setProperty('--primary','#00af41')
    }
  }

  function handleAnimate(){
    setAnimate(false)
  }

  document.onkeydown = (e) => {
    e.preventDefault()
    if(e.key === "ArrowLeft"){
      handler(num-1)
    }
    if(e.key === "ArrowRight"){
      handler(num+1)
    }
  }

  return (
    <div className="App">
      <h1>Timeline 1907 - 1948</h1>
      <Timeline data={d} handler={handler} />
      <Info data={d} num={num} handler={handler} animate={animate} handleAnimate={handleAnimate}/>
      <div className="shadow left"></div>
      <div className="shadow right"></div>
    </div>

  );
}

export default App;
